import React, { useEffect, useState } from "react";
import IntroLoading from "../Components/IntroLoading";
import Background from "../Layout/Background";
import MainComponets from "../Layout/MainComponets";
import { motion } from "framer-motion";
import { AccessTime } from "@mui/icons-material";
import SharingPage from "../Layout/SharingPage";
import MainComponets2 from "../Layout/MainComponent2";

const Main2 = () => {
  const [intro, setIntro] = useState(true);
  //   const [start, setStart] = useState(true);
  const [sharingPage, setSharingPage] = useState(false);
  const [data, setData] = useState({});
  useEffect(() => {
    setTimeout(() => setIntro(false), 2000);
  }, []);
  return (
    <>
      <Background />
      {intro ? (
        <IntroLoading />
      ) : sharingPage ? (
        <div
          className="flex"
          style={{
            position: "fixed",
            height: "100vh",
            width: "100vw",
            zIndex: 99999999,
          }}
        >
          <div className="main_pages main_component">
            <motion.div
              className="question"
              initial={{ y: 50 }}
              animate={{
                y: 0,
              }}
              transition={{
                type: "spring",
                stiffness: 100,
                duration: "1",
              }}
            >
              <SharingPage data={data} />
            </motion.div>
          </div>
        </div>
      ) : (
        <MainComponets2 setForm={setSharingPage} setData={setData} />
      )}
    </>
  );
};

export default Main2;
