/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react'
import gpay from '../assets/svg/gpay.svg';
import phonepe from '../assets/svg/phonepe.svg';
import paytm from '../assets/svg/paytm.svg';
import upiIcon from '../assets/svg/upi-icon.svg';
import spinner from '../assets/svg/spinner.svg';
import { ChevronRightRounded as ArrowRight } from '@mui/icons-material'
import axios from 'axios';

const Payment = ({ BTNLabel, onPaymentSuccess, userMoblile }) => {
    const [showOverlay, setShowOverlay] = useState(false)
    return (
        <div id="payment-component-wrapper">
            <button id='payment-btn' onClick={() => setShowOverlay(true)}>{BTNLabel}</button>
            {showOverlay && <PaymentPopup
                onPaymentSuccess={onPaymentSuccess}
                close={() => { clearInterval(txnStatusTimer); setShowOverlay(false) }}
                userMoblile={userMoblile}
            />}
        </div>
    )
}

let txnStatusTimer;
const PaymentBtns = [
    { uri_prefix: "gpay", logo: gpay, label: "Google Pay" },
    { uri_prefix: "phonepe", logo: phonepe, label: "PhonePe" },
    { uri_prefix: "paytm", logo: paytm, label: "Paytm" }
]

export const PaymentPopup = ({ close, onPaymentSuccess, userMoblile }) => {

    const TRANSACTIONSERVER = 'https://api.myfooddo.com'
    const BACKENDSERVER = axios.defaults.baseURL
    console.log(BACKENDSERVER)

    const [paymentBTNState, setPaymentBTNState] = useState({ status: false })
    const [loading, setLoading] = useState(true)

    const callSelf = method => { method() }
    const postTransactionRecord = async merchantTranId => {
        try {
            const URL = `${BACKENDSERVER}Registerations/record-transaction`
            const res = await axios.post(URL, { merchantTranId, userMoblile })
            if (res.status === 400 || res.status === 500)
                setTimeout(() => callSelf(() => postTransactionRecord(merchantTranId)), 1000);
        } catch (error) {
            console.log({ error })
        }
    }

    const validateTxn = async merchantTranId => {

        clearInterval(txnStatusTimer)
        txnStatusTimer = setInterval(async () => {

            const URL = `${TRANSACTIONSERVER}/txnstatus`
            const res = await axios.post(URL, { merchantTranId });

            if (res?.data?.status === 'SUCCESS') {
                postTransactionRecord()
                clearInterval(txnStatusTimer)
                setPaymentBTNState({ status: false })
                close()
                onPaymentSuccess && onPaymentSuccess()
                localStorage.setItem(userMoblile || '<REGISTERED-USER-MOBILE-HERE>', merchantTranId)
            }
            else if (res?.data?.status === 'FAILURE' || res?.data?.status === 'FAIL') {
                clearInterval(txnStatusTimer)
                setPaymentBTNState({ status: false, failed: true })
            }
            else if (res?.data?.status === 'PENDING' && paymentBTNState?.warning) {
                setPaymentBTNState(doc => ({ ...doc, warning: '' }))
            }
        }, 5000)
    }

    const callApi = async () => {
        const URL = `${TRANSACTIONSERVER}/upi_payments/yoga-for-youth`
        let res = await axios.post(URL, {});
        if (res.data?.txnId) {
            if (navigator.userAgent.indexOf("like Mac") === -1)
                window.location.href = res.data?.qrString

            validateTxn(res.data?.txnId);
            setPaymentBTNState({
                status: true,
                txnId: res.data?.txnId,
                qrString: res.data?.qrString
            })
            setLoading(false)
        }
    }

    useEffect(() => {
        callApi()
    }, [])

    return (
        <div
            className='overlay-wrapper'
            // onClick={e => !e?.nativeEvent?.path?.find(i => i.id === 'payment-popup') ? close() : ''}
        >
            <div id='payment-popup'>
                {loading ?
                    <div id='payment-spinner-wrapper'>
                        <img src={spinner} alt="spinner" />
                    </div>
                    : paymentBTNState?.failed ?
                        <div id='payment-failed'>
                            <h5>Transaction Failed</h5>
                            <button onClick={callApi}>Retry</button>
                        </div>
                        : paymentBTNState?.status && paymentBTNState?.txnId && (
                            navigator.userAgent.indexOf("like Mac") === 1 ?
                                <div id='payment-android'>
                                    <img src={spinner} alt="spinner" />
                                    <a href={paymentBTNState?.qrString || ''}>
                                        <img src={upiIcon} alt="upi" /><span>Pay Using UPI</span>
                                    </a>
                                </div>
                                :
                                <div id="payment-actions">
                                    {PaymentBtns?.map(i => (
                                        <button className="payment-btn">
                                            <a href={`${TRANSACTIONSERVER}/pay_link/${paymentBTNState?.txnId}/redirect&appName=${i?.uri_prefix}`}>
                                                <div><img src={i.logo} alt="upi-payment-app" width="48" height="48" />{i.label}</div>
                                                <ArrowRight />
                                            </a>
                                        </button>
                                    ))}
                                </div>
                        )
                }
            </div>
        </div >
    )
}

export default Payment