import axios from "axios";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import Main from "./Pages/Main";
import { v4 as uuid } from "uuid";
import { useEffect } from "react";
import Home from "./Pages/Home";
import Admin from "./Pages/Admin";
import Registerations from "./Pages/reports/Registerations";
import LoginPage from "./Pages/LoginPage";
import Whatsapp from "./Pages/Whatsapp";
import InstagramPage from "./Pages/Instagram";
import Main2 from "./Pages/Main2";
function App() {
  axios.defaults.baseURL = "http://52.66.131.56:9000/";
  // axios.defaults.baseURL = "http://localhost:9000/";

  const newEntity = async (newPersone) => {
    const response = await axios({
      method: "post",
      url: "/Details/newEntry",
      data: { newPersone },
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (response.data.success) {
      if (newPersone) {
        let identification = uuid();
        localStorage.setItem("identification", identification);
      }
    }
  };

  useEffect(() => {
    if (
      window.location.pathname.includes("register") ||
      (window.location.pathname.includes("admin") &&
        !localStorage.getItem("admin"))
    )
      newEntity(!localStorage.getItem("identification"));
  }, []);
  return (
    <div
      className="App"
      onTouchMove={(e) => {
        console.log(e.isPropagationStopped());
      }}
    >
      <Router>
        <Routes>
          <Route path="*" element={<Navigate replace to={"/register"} />} />
          <Route path="/registerold" element={<Main />} />
          <Route path="/register" element={<Main2 />} />
          <Route path="/whatsapp" element={<Whatsapp />} />
          <Route path="/instagram" element={<InstagramPage />} />
          <Route path="/instagram" element={<Whatsapp />} />
          <Route path="/home" element={<Home />} />
          {localStorage.getItem("admin") ? (
            <>
              <Route path="/admin" element={<Admin />} />
              <Route
                path="/admin/regesterations"
                element={<Registerations />}
              />
            </>
          ) : (
            <>
              <Route path="/admin" element={<LoginPage />} />
              <Route
                path="/admin"
                element={<Navigate replace to={"/login"} />}
              />
            </>
          )}
        </Routes>
      </Router>
    </div>
  );
}

export default App;
