import React, { useEffect, useRef, useState } from "react";
import Logo from "../assets/frame-yoga-youth.png";
import html2canvas from "html2canvas";
import { Button } from "@mui/material";
import axios from "axios";
import { motion } from "framer-motion";
import { NextPlan, SkipNext, Image } from "@mui/icons-material";
const SharingPage = () => {
  const [userData, setUserData] = useState({});
  const [img, setimg] = useState("");

  const componentRef = useRef();
  const getData = async () => {
    const result = await axios({
      url:
        "/Registerations/getRegistration/" +
        localStorage.getItem("identification"),
      method: "get",
      headers: { "Content-Type": "multipart/form-data" },
    });
    if (result.data.success) {
      setUserData(result.data.result);
    }
  };
  useEffect(() => {
    getData();
  }, []);
  useEffect(() => {
    // let url = data?.photos[0];
    let url = userData?.photos?.length
      ? "/" + userData?.photos[0].split("/")[3] + ".png"
      : "";
    setTimeout(() => {
      axios.get(url, { responseType: "blob" }).then(function (response) {
        var reader = new window.FileReader();
        reader.readAsDataURL(response.data);
        reader.onload = function () {
          var imageDataUrl = reader.result;
          setimg(imageDataUrl);
        };
      });
    }, 4000);
  }, [userData?.photos]);
  const submitHandler = async (newImg) => {
    setimg("");
    let itemData = {
      registration_uuid: localStorage.getItem("identification"),
    };
    let url = await axios.get("s3url");
    url = url.data.url;

    const result = await axios({
      url,
      method: "put",
      headers: { "Content-Type": "multipart/form-data" },
      data: newImg,
    });
    if (result.status === 200) {
      let photos = url?.split("?")[0];
      itemData = {
        ...itemData,
        photos: [photos],
      };
      const response = await axios({
        method: "put",
        url: "/Registerations/putRegistration",
        data: itemData,
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (response.data.success) {
        getData();
      }
    }
  };
  const handlePng = async () => {
    const element = componentRef.current;
    console.log(element);
    const canvas = await html2canvas(element);

    const data = canvas.toDataURL("image/jpg");
    const link = document.createElement("a");

    if (typeof link.download === "string") {
      link.href = data;
      link.download = "image.jpg";

      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      await navigator.share({
        title: "Share API",
        text: "This API is cool!",
        url: link,
      });
    } else {
      window.open(data);
    }
  };
  return (
    <div className="flex sharing_page">
      <div className="sharing_page_image" ref={componentRef}>
        <img
          src={Logo}
          alt=""
          className="sharing_logo_img"
          style={{ width: "300px" }}
        />
        {img ? (
          <img
            src={img}
            alt=""
            id="sharing_person_img"
            style={{ objectFit: "cover" }}
          />
        ) : (
          <div id="sharing_person_img" className="flex">
            Please Wait
          </div>
        )}
      </div>
      {img ? (
        <>
          <Button
            // variant="containe"
            style={{
              backgroundColor: "rgba(142,198,13,255)",
              fontSize: "25px",
              color: "#fff",
            }}
            onClick={handlePng}
          >
            Download
          </Button>
          <motion.label
            initial={{ y: 50 }}
            animate={{
              y: 0,
            }}
            htmlFor="upload_logo"
            transition={{
              type: "spring",
              stiffness: 100,
              duration: "1",
            }}
            style={{
              justifyContent: "center",
              marginTop: "30px",
              width: "100%",
              fontSize: "30px",
            }}
            // onClick={() => (img ? window.location.assign("whatsapp") : {})}
          >
            <u>Change Image</u>

            {/* {newImg ? (
              <>
                <img
                  src={URL.createObjectURL(newImg)}
                  className="previwImages"
                  alt="yourimage"
                  style={{ width: "100px", objectFit: "contain" }}
                />
              </>
            ) : (
              ""
            )} */}
            <input
              id="upload_logo"
              accept="image/png, image/gif, image/jpeg"
              type="file"
              style={{ display: "none" }}
              onChange={(e) => {
                submitHandler(e.target.files[0]);
              }}
            />
          </motion.label>
        </>
      ) : (
        <Button
          variant="contained"
          style={{
            backgroundColor: "rgba(142,198,13,255)",
            fontSize: "25px",
          }}
        >
          <svg viewBox="0 0 100 100">
            <path
              d="M10 50A40 40 0 0 0 90 50A40 44.8 0 0 1 10 50"
              fill="#ffffff"
              stroke="none"
            >
              <animateTransform
                attributeName="transform"
                type="rotate"
                dur="1s"
                repeatCount="indefinite"
                keyTimes="0;1"
                values="0 50 51;360 50 51"
              ></animateTransform>
            </path>
          </svg>
        </Button>
      )}

      {
        // newImg ? (
        //   <Button
        //     variant="contained"
        //     style={{
        //       backgroundColor: "rgba(142,198,13,255)",
        //       fontSize: "25px",
        //     }}
        //     onClick={submitHandler}
        //   >
        //     upload
        //   </Button>
        // ) :
        img ? (
          <motion.div
            initial={{ y: 50 }}
            animate={{
              y: 0,
            }}
            transition={{
              type: "spring",
              stiffness: 100,
              duration: "1",
            }}
            style={{
              justifyContent: "center",
              marginTop: "30px",
              width: "100%",
              fontSize: "30px",
            }}
            onClick={() => (img ? window.location.assign("whatsapp") : {})}
          >
            <SkipNext /> <did>Next</did>
          </motion.div>
        ) : (
          "Please Wait..."
        )
      }
    </div>
  );
};

export default SharingPage;
