import React, { useState } from "react";
import { data, RegData } from "../data";
import { motion } from "framer-motion";
import { ArrowForward, Check, Image } from "@mui/icons-material";
import { Button, Input, NativeSelect } from "@mui/material";
import { useSwipeable } from "react-swipeable";
import axios from "axios";
import Payment from "../Components/payment";

const MainComponets = ({ setForm, setData }) => {

  const [position, setPosition] = useState(4);
  const [userData, setUserData] = useState(data);
  const [regData, setRegData] = useState(RegData);
  const [transition, setTrasition] = useState("");
  const [layout, setLayout] = useState("");
  const [isLoading, setIsloading] = useState(false);

  const ErrorAnimation = (name) => {
    setTimeout(() => setTrasition({ name, error: true }), 500);
    setTimeout(() => setTrasition({ name, error: false }), 1000);
    setTimeout(() => setTrasition({ name, error: true }), 1500);
    setTimeout(() => setTrasition({ name, error: false }), 2000);
    setTimeout(() => setTrasition({ name, error: true }), 2500);
  };

  const submitHandler = async () => {
    setIsloading(true);
    let itemData = regData;
    let url = await axios.get("s3url");
    url = url.data.url;

    const result = await axios({
      url,
      method: "put",
      headers: { "Content-Type": "multipart/form-data" },
      data: itemData?.image,
    });

    if (result.status === 200) {
      let photos = url?.split("?")[0];
      itemData = {
        ...itemData,
        photos: [photos],
      };
      const response = await axios({
        method: "put",
        url: "/Registerations/putRegistration",
        data: itemData,
        headers: {
          "Content-Type": "application/json",
        },
      });
      if (response.data.success) {
        setForm(true);
        setData(response.data.result);
      }
    }
    setIsloading(false);
  };

  const submitPartOneHandler = async () => {
    setIsloading(true);
    let itemData = regData;

    const response = await axios({
      method: "post",
      url: "/Registerations/postRegistration",
      data: itemData,
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (response.data.success) {
      setPosition(4.5);
    }
    setIsloading(false);
  };

  const handlers = useSwipeable({

    onSwipedUp: () => {
      setPosition((prev) => {
        let item = userData.find((a) => a.index === prev);
        console.log(item);
        if (!item.value) {
          setUserData((prev) => prev.map((a) => a.index === item.index ? { ...a, error: true } : a));
          ErrorAnimation(item?.name);
          return prev;
        } else if (prev === 4) return 4.5;
        else if (prev === 4.5) return 5;
        else return prev + 2 === userData.length ? prev : prev + 1;
      });
      console.log("swipeup");
    },

    onSwipedDown: () => {
      setPosition((prev) => (prev === 0 ? prev : prev - 1));
      console.log("swipedown");
    },

    delta: 60, // min distance(px) before a swipe starts. *See Notes*
    preventScrollOnSwipe: true, // prevents scroll during swipe (*See Details*)
    trackTouch: true, // track touch input
    trackMouse: true, // track mouse input
    rotationAngle: 0, // set a rotation angle
    swipeDuration: Infinity, // allowable duration of a swipe (ms). *See Notes*
    touchEventOptions: { passive: true },
  });

  console.log("layout", layout)

  return (
    <div
      className="flex"
      style={{
        position: "fixed",
        height: "100vh",
        width: "100vw",
        zIndex: 99999999,
      }}
      {...handlers}
    >
      <div className="main_pages main_component" style={layout ? { height: "200px" } : {}}>
        {userData.map((item) => (
          <motion.div
            initial={{ rotate: 1 }}
            animate={{
              rotate: 0,
              top: `${(item.index - (position || 0)) * 1000}vh`,
            }}
            transition={{
              type: "tween",
              bounceStifafness: 260,
              bounceDamping: 20,
            }}
            className="container"
          >
            {item?.img ? (
              <div className="question">
                <img src={item?.img} alt="" style={{ maxWidth: "400px" }} />
              </div>
            ) : (
              <>
                <div
                  className="question"
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "start",
                    flexDirection: "row",
                    width: "100%",
                  }}
                >
                  <div>{+item.index + 1} </div>
                  <ArrowForward fontSize="2px" style={{ marginTop: "5px" }} />
                  <div
                    style={{
                      width: "80%",
                      fontSize: "25px",
                      textAlign: "left",
                    }}
                  >
                    {item.heading}
                    {item?.requird ? "*" : ""}
                  </div>
                </div>
                {item?.subHeading ? (
                  <div
                    className="question"
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "end",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <div
                      style={{
                        width: "95%",
                        fontSize: "20px",
                        fontWeight: "400",
                        textAlign: "left",
                        color: "gray",
                      }}
                    >
                      {item.subHeading}
                    </div>
                  </div>
                ) : (
                  ""
                )}
              </>
            )}
            {item?.type === "text" || item?.type === "number" ? (
              <Input
                onFocus={() => {
                  setLayout(true);
                }}
            
                style={{ border: "rgba(142,198,13,255)", fontSize: "25px" }}
                placeholder={item.placeholder}
                type={item?.type}
                value={item.value}
                error={transition?.name === item.name && transition?.error}
                multiline={item?.multiline}
                onChange={(e) => {
                  if (item?.type !== "number" || e.target.value.length <= 10) {
                    setUserData((prev) =>
                      prev.map((a) =>
                        a.index === item.index
                          ? { ...a, value: e.target.value }
                          : a
                      )
                    );
                    setRegData((prev) => ({
                      ...prev,
                      [item.name]:
                        item?.name === "mobile"
                          ? [e.target.value]
                          : e.target.value,
                    }));
                  }
                }}
              />
            ) : item?.type === "select" ? (
              <NativeSelect
                style={{
                  border: "rgba(142,198,13,255)",
                  fontSize: "25px",
                  width: "80%",
                  zIndex: "9999999999999",
                }}
                labelId="demo-simple-select-standard-label"
                id="demo-simple-select-standard"
                value={item.value}
                error={transition?.name === item.name && transition?.error}
                onChange={(e) => {
                  setUserData((prev) =>
                    prev.map((a) =>
                      a.index === item.index
                        ? { ...a, value: e.target.value }
                        : a
                    )
                  );
                  setRegData((prev) => ({
                    ...prev,
                    [item.name]: e.target.value,
                  }));
                }}
                label="Age"
              >
                <option value="">
                  <em>None</em>
                </option>
                {item?.options.map((a) => (
                  <option value={a}>{a}</option>
                ))}
              </NativeSelect>
            ) : item?.type === "button"
              ? (
                item?.payment_btn
                  ? <Payment BTNLabel={item?.options?.[0]} /> :
                  (
                    <div
                      style={{
                        display: "flex",
                        alignItem: "start",
                        justifyContent: "center",
                        flexDirection: "column",
                        width: "100%",
                      }}
                    >
                      {item.options.map((b) => (
                        <Button
                          style={
                            item.value === b
                              ? {
                                color: "rgba(142,198,13,255)",
                                border: "4px solid rgba(142,198,13,255)",

                                margin: "10px 0",
                                width: "300px",
                                textAlign: "left",
                                fontWeight: "1000",
                                backgroundColor: "#000",
                              }
                              : {
                                backgroundColor: "#fff",
                                color: "rgba(142,198,13,255)",
                                border: "3px solid rgba(142,198,13,255)",
                                margin: "10px 0",
                                width: "300px",
                                textAlign: "left",
                                fontWeight: "900",
                              }
                          }
                          onClick={(e) => {
                            setUserData((prev) => prev.map((a) => a.index === item.index ? { ...a, value: b } : a));
                            setRegData((prev) => ({ ...prev, [item.name]: b }));
                          }}
                        >
                          <span
                            className="flex"
                            style={
                              item.value === b
                                ? {
                                  color: "rgba(142,198,13,255)",

                                  border: "4px solid rgba(142,198,13,255)",

                                  width: "20px",
                                  height: "20px",
                                  marginRight: "10px",
                                }
                                : {
                                  color: "rgba(142,198,13,255)",

                                  border: "3px solid rgba(142,198,13,255)",

                                  width: "20px",
                                  height: "20px",
                                  marginRight: "10px",
                                }
                            }
                          >
                            {b[0]}
                          </span>
                          <span style={{ width: "250px" }}>{b}</span>
                          {item?.value === b ? (
                            <span
                              style={{
                                padding: "0 10px",
                                fontSize: "5px",
                                marginLeft: "10px",
                              }}
                            >
                              <Check />
                            </span>
                          ) : (
                            ""
                          )}
                        </Button>
                      ))}
                    </div>
                  )
              ) : item?.type === "image" ? (
                <label
                  htmlFor="upload_logo"
                  className="flex"
                  style={{
                    // color: "rgba(142,198,13,255)",
                    // border: "4px solid #fff",
                    padding: "10px",
                    backgroundColor: "rgba(142,198,13,255)",
                    margin: "10px 0",
                    width: "300px",
                    textAlign: "left",
                    fontSize: "30px",
                    fontWeight: "1000",
                    color: "#fff",
                  }}
                >
                  <span style={{ width: "250px" }} className="flex">
                    <Image />
                    Click Here
                  </span>
                  {item.value ? (
                    <img
                      src={URL.createObjectURL(item.value)}
                      className="previwImages"
                      alt="yourimage"
                      style={{ width: "100px", objectFit: "contain" }}
                    />
                  ) : (
                    ""
                  )}
                  <input
                    id="upload_logo"
                    type="file"
                    style={{ display: "none" }}
                    onChange={(e) => {
                      setUserData((prev) =>
                        prev.map((a) =>
                          a.index === item.index
                            ? { ...a, value: e.target.files[0] }
                            : a
                        )
                      );
                      setRegData((prev) => ({
                        ...prev,
                        image: e.target.files[0],
                      }));
                    }}
                  />
                </label>
              ) : (
                ""
              )}
            {item.secondHeading && item.value ? (
              <>
                <div
                  className="question"
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "start",
                    flexDirection: "row",
                    width: "100%",
                    marginTop: "60px",
                  }}
                >
                  <ArrowForward fontSize="2px" style={{ marginTop: "5px" }} />
                  <div
                    style={{
                      width: "80%",
                      fontSize: "20px",
                      textAlign: "left",
                    }}
                  >
                    {item.secondHeading}
                  </div>
                </div>
                <div
                  style={{
                    display: "flex",
                    alignItem: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                    width: "100%",
                  }}
                >
                  {item.secondOptions.map((b) => (
                    <Button
                      style={
                        item.secondValue === b
                          ? {
                            color: "rgba(142,198,13,255)",
                            border: "4px solid rgba(142,198,13,255)",
                            backgroundColor: "black",
                            margin: "10px 0",
                            width: "300px",
                            textAlign: "left",
                            fontWeight: "1000",
                          }
                          : {
                            backgroundColor: "#fff",
                            color: "rgba(142,198,13,255)",
                            border: "3px solid rgba(142,198,13,255)",
                            margin: "10px 0",
                            width: "300px",
                            textAlign: "left",
                            fontWeight: "900",
                          }
                      }
                      onClick={(e) => {
                        setUserData((prev) =>
                          prev.map((a) =>
                            a.index === item.index
                              ? { ...a, secondValue: b }
                              : a
                          )
                        );
                        setRegData((prev) => ({
                          ...prev,
                          [item.secondName]: b,
                        }));
                      }}
                    >
                      <span
                        className="flex"
                        style={
                          item.secondValue === b
                            ? {
                              color: "rgba(142,198,13,255)",

                              border: "4px solid rgba(142,198,13,255)",

                              width: "20px",
                              height: "20px",
                              marginRight: "10px",
                            }
                            : {
                              color: "rgba(142,198,13,255)",

                              border: "3px solid rgba(142,198,13,255)",

                              width: "20px",
                              height: "20px",
                              marginRight: "10px",
                            }
                        }
                      >
                        {b[0]}
                      </span>
                      <span style={{ width: "250px" }}>{b}</span>
                      {item?.secondValue === b ? (
                        <span
                          style={{
                            padding: "0 10px",
                            fontSize: "5px",
                            marginLeft: "10px",
                          }}
                        >
                          <Check />
                        </span>
                      ) : (
                        ""
                      )}
                    </Button>
                  ))}
                </div>
              </>
            ) : (
              ""
            )}
            {!isLoading ? (
              <Button
                variant="contained"
                style={{
                  backgroundColor: "rgba(142,198,13,255)",
                  fontSize: "25px",
                }}
                onClick={() => {
                  setLayout(false);
                  if (
                    (item?.name === "mobile" && item?.value.length !== 10) ||
                    !item.value
                  ) {
                    setUserData((prev) =>
                      prev.map((a) =>
                        a.index === item.index ? { ...a, error: true } : a
                      )
                    );
                    ErrorAnimation(item?.name);
                    return;
                  } else if (position === 4) {
                    submitPartOneHandler();
                  } else if (position + 2 === userData.length) {
                    submitHandler();
                    return;
                  } else
                    setPosition((prev) =>
                      prev === 4
                        ? 4.5
                        : prev === 4.5
                          ? 5
                          : prev + 2 === userData?.length
                            ? prev
                            : prev + 1
                    );
                }}
              >
                {item.type === "message" ? "Cool" : "Ok"}
              </Button>
            ) : (
              <Button
                variant="contained"
                style={{
                  backgroundColor: "rgba(142,198,13,255)",
                  fontSize: "25px",
                }}
              >
                <svg viewBox="0 0 100 100">
                  <path
                    d="M10 50A40 40 0 0 0 90 50A40 44.8 0 0 1 10 50"
                    fill="#ffffff"
                    stroke="none"
                  >
                    <animateTransform
                      attributeName="transform"
                      type="rotate"
                      dur="1s"
                      repeatCount="indefinite"
                      keyTimes="0;1"
                      values="0 50 51;360 50 51"
                    ></animateTransform>
                  </path>
                </svg>
              </Button>
            )}
          </motion.div>
        ))}
      </div>
    </div>
  );
};

export default MainComponets;
