import React, { useEffect, useState } from "react";
import IntroLoading from "../Components/IntroLoading";
import Background from "../Layout/Background";
import MainComponets from "../Layout/MainComponets";
import { motion } from "framer-motion";
import { AccessTime } from "@mui/icons-material";
import SharingPage from "../Layout/SharingPage";

const Main = () => {

  const [intro, setIntro] = useState(true);
  const [start, setStart] = useState(true);
  const [sharingPage, setSharingPage] = useState(false);
  const [data, setData] = useState({});

  useEffect(() => {
    setTimeout(() => setIntro(false), 2000);
  }, []);

  return (
    <>
      <Background />
      {intro ? (
        <IntroLoading />
      ) : start ? (
        <div className="main_pages">
          <motion.div
            className="question"
            initial={{ y: 50 }}
            animate={{
              y: 0,
            }}
            transition={{
              type: "spring",
              stiffness: 100,
              duration: "1",
            }}
          >
            Would you like to be a part of Yoga for Youth?
          </motion.div>
          <div className="options">
            <motion.button
              onClick={() => setStart(false)}
              initial={{ y: 50 }}
              animate={{
                y: 0,
              }}
              transition={{
                type: "spring",
                stiffness: 100,
                duration: "1",
              }}
            >
              Absolutly
            </motion.button>
            <motion.div
              initial={{ y: 50 }}
              animate={{
                y: 0,
              }}
              transition={{
                type: "spring",
                stiffness: 100,
                duration: "1",
              }}
            >
              <AccessTime /> <did>Takes 3 min</did>
            </motion.div>
          </div>
        </div>
      ) : sharingPage ? (
        <div
          className="flex"
          style={{
            position: "fixed",
            height: "100vh",
            width: "100vw",
            zIndex: 99999999,
          }}
        >
          <div className="main_pages main_component">
            <motion.div
              className="question"
              initial={{ y: 50 }}
              animate={{ y: 0 }}
              transition={{
                type: "spring",
                stiffness: 100,
                duration: "1",
              }}
            >
              <SharingPage data={data} />
            </motion.div>
          </div>
        </div>
      ) : (<MainComponets setForm={setSharingPage} setData={setData} />)}
    </>
  );
};

export default Main;
