import React, { useEffect, useState } from "react";
import Background from "../Layout/Background";
import { motion } from "framer-motion";
import { SkipNext, WhatsApp } from "@mui/icons-material";
import axios from "axios";

const WhatsappPage = () => {
  const [userData, setUserData] = useState();
  const getData = async () => {
    const result = await axios({
      url:
        "/Registerations/getRegistration/" +
        localStorage.getItem("identification"),
      method: "get",
      headers: { "Content-Type": "multipart/form-data" },
    });
    if (result.data.success) {
      setUserData(result.data.result);
    }
  };
  useEffect(() => {
    getData();
  }, []);
  return (
    <>
      <Background />

      <div className="main_pages">
        <motion.h1
          className="question"
          style={{ fontSize: "25px" }}
          initial={{ y: 50 }}
          animate={{
            y: 0,
          }}
          transition={{
            type: "spring",
            stiffness: 100,
            duration: "1",
          }}
        >
          You can join YfY Page Group
          by  <br />clicking below.
        </motion.h1>
        <motion.h1
          className="question"
          style={{
            width: "95%",
            fontSize: "20px",
            fontWeight: "400",
            color: "gray",
          }}
          initial={{ y: 50 }}
          animate={{
            y: 0,
          }}
          transition={{
            type: "spring",
            stiffness: 100,
            duration: "1",
          }}
        >
         We will post updates about
           the event<br /> in this group.
        </motion.h1>
        <div className="options">
          <motion.button
            onClick={() =>
              window
                .open(
                  `https://api.whatsapp.com/send?phone=919021581947&text=Hi%2C%0APlease%20add%20me%20to%20*${
                    userData?.gender === "Male" ? "Boys" : "Girls"
                  }*%20whatsapp%20group%20of%20Yoga%20for%20Youth.%0ARegistration%20Number%3A%20${
                    userData?.registration_number
                  }%0AName%3A%20${userData?.full_name}`
                )
                .focus()
            }
            initial={{ y: 50 }}
            animate={{
              y: 0,
            }}
            transition={{
              type: "spring",
              stiffness: 100,
              duration: "1",
            }}
          >
            <WhatsApp /> Join Whatsapp Group
          </motion.button>
          <motion.div
            initial={{ y: 50 }}
            animate={{
              y: 0,
            }}
            transition={{
              type: "spring",
              stiffness: 100,
              duration: "1",
            }}
            style={{
              justifyContent: "center",
              marginTop: "30px",
              width: "100%",
              fontSize: "30px",
            }}
            onClick={() => window.location.assign("instagram")}
          >
            <SkipNext /> <did>Next</did>
          </motion.div>
        </div>
      </div>
    </>
  );
};

export default WhatsappPage;
